@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  color: #292830;
  font-size: 16px;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

::-webkit-scrollbar {
  display: none;
}

.list-highlight {
  background: #e0e0e0;
  border-right: 2px solid #000000;
}

.alert-modal {
  position: absolute;
  background: #fff;
  width: auto;
  top: 20%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 20px;
  z-index: 100;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.8);
  border: 2px solid #151515;
  border-radius: 5px;
  .close-alert {
    text-align: right;
    margin-top: -20px;
    margin-right: -15px;
    small {
      font-size: 15px;
      cursor: pointer;
    }
  }
  p {
    margin: 10px 0;
  }
  .alert-btn {
    text-align: center;
    button {
      border: none;
      background: #151515;
      color: #fff;
      padding: 5px 10px;
      border-radius: 3px;
    }
    button:hover {
      color: #151515;
      background: #f2f2f2;
      transition: 0.5s;
    }
  }
}

.message-modal {
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 100;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.8);
  border: 2px solid #151515;
  position: absolute;
  min-width: 300px;
  top: 10%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #fff;
  .message {
    font-size: 18px;
  }
  small {
    font-size: 15px;
    cursor: pointer;
  }
}

/* Heading */
.header {
  background: #151515;
  padding: 15px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #ffffff;
  img {
    height: 3vh;
  }
  a {
    text-decoration: none;
    color: #ffffff;
  }
  a:hover {
    color: #ffffff;
  }
  i {
    cursor: pointer;
  }
  i:hover {
    color: #b6b4c5;
    transform: scale(1.2);
    transition: 0.5s;
  }
  .toggle {
    cursor: pointer;
  }
  .toggle:hover {
    transform: scale(1.2);
    transition: 0.5s;
  }
}

/* Main container */
.main {
  height: 94.75vh;
  display: flex;
  line-height: 30px;
  letter-spacing: 0.5px;
  /* side-bar */
  .side-bar {
    position: relative;
    width: 400px;
    background: #ffffff;
    overflow-y: scroll;
    .search-container {
      text-align: center;
      margin: 10px;
      #search {
        width: 100%;
        margin: 10px auto;
        padding: 10px;
        border-radius: 2px;
        border: 0.5px solid #c1c1c1;
        font-size: 16px;
      }
    }
    ul {
      list-style: none;
      font-weight: 500;
      li {
        padding: 20px 0 20px 30px;
        cursor: pointer;
      }
      li:hover {
        background: #e0e0e0;
        transform: scale(1.03);
        transition: 0.5s;
      }
      a {
        text-decoration: none;
        color: #000;
      }
    }
  }

  /* CONTENT */
  .content {
    width: 100%;
    background: #f2f2f2;
    padding: 50px;
    overflow-y: scroll;

    .content-heading {
      display: flex;
      justify-content: space-between;
      a {
        text-decoration: none;
        color: #000;
      }
      i {
        margin-left: 10px;
        cursor: pointer;
      }
      i:hover {
        color: #292830;
        transform: scale(1.2);
        transition: 0.5s;
      }
      h2 {
        margin: 25px 0;
      }
    }
    .player-info {
      margin: 40px 0 15px;
      img {
        max-width: 100%;
        margin: 10px 40px 20px;
        border-radius: 5px;
        box-shadow: 5px 5px 5px rgba($color: #000000, $alpha: 0.5);
      }
    }
    .home {
      margin: 0 auto;
      img {
        width: 100%;
        margin: 40px 0;
        border-radius: 5px;
      }
    }

    /* Form */
    form {
      margin: 30px 0;
      #name,
      #update-name {
        padding: 10px;
        font-size: 16px;
        border-radius: 2px;
        border: 0.5px solid #c1c1c1;
        margin: 10px 0 20px;
        outline: none;
        width: 100%;
      }
      .buttons {
        text-align: right;
        margin: 20px 0;

        button {
          font-size: 16px;
          background: #292830;
          border-radius: 2px;
          color: #ffffff;
          border: none;
          padding: 10px 30px;
          margin-left: 10px;
          cursor: pointer;
        }
        button:hover {
          background: #333333;
          transform: scale(1.03);
        }
      }
    }
  }
}

@media only screen and (max-width: 1024px) {
  /* Main container */
  .main {
    display: flex;
    letter-spacing: 0.3px;
    /* side-bar */
    .side-bar {
      .search-container {
        text-align: center;
        margin: 5px 10px;

        #search {
          width: 100%;
          margin: 5px auto;
          padding: 10px;
          font-size: 15px;
        }
      }
    }

    /* CONTENT */
    .content {
      padding: 25px;

      h1 {
        font-size: 25px;
      }

      .content-heading {
        i {
          margin: 10px 5px;
        }
        h2 {
          margin: 10px 0;
          font-size: 25px;
        }
      }
      .player-info {
        margin: 20px 0 5px;
        font-size: 15px;
        img {
          margin: 10px;
        }
      }
      .home {
        margin: 50px auto;
        img {
          margin: 20px;
          border-radius: 5px;
        }
      }

      /* Form */
      form {
        margin: 10px 0;
      }
    }
  }
}

@media only screen and (max-width: 480px) {
  /* Main container */
  .main {
    display: block;
    line-height: 10px;
    letter-spacing: 0.3px;
    /* side-bar */
    .side-bar {
      width: 100%;
      height: 300px;
      background: rgb(200, 200, 200);
      .search-container {
        text-align: center;
        margin: 5px;

        #search {
          width: 100%;
          margin: 5px auto;
          padding: 20px;
          font-size: 15px;
        }
      }
    }

    /* CONTENT */
    .content {
      padding: 15px;
      background: #fff;

      h1 {
        font-size: 25px;
      }

      .content-heading {
        i {
          margin: 10px 5px;
          cursor: pointer;
        }
        h2 {
          margin: 10px 0;
          font-size: 25px;
        }
      }
      .player-info {
        margin: 20px 0 5px;
        font-size: 15px;
        img {
          margin: 0 5px 5px;
          border-radius: 2px;
        }
      }
      .home {
        img {
          margin: 20px 0;
        }
      }

      /* Form */
      form {
        margin: 10px 0;
      }
    }
  }
}
